function VitalCard({ type, systolic, diastolic, value, status }) {
  const isBP = type === 'blood-pressure';
  const title = isBP ? '' : '';
  const icon = isBP ? '' : '';
  const display = isBP ? `${systolic} / ${diastolic}` : `${value}`;

  return (
    <div className="card">
      <div className="row between">
        <h3>{icon} {title}</h3>
        <span className="status-badge">{status}</span>
      </div>
      <div className="result-number">{display}</div>
      <svg width="100%" height="40" viewBox="0 0 200 40">
        <path d="M0,20 C20,5 40,35 60,20 C80,5 100,35 120,20 C140,5 160,35 180,20" stroke="#adb5bd" fill="none" />
      </svg>
      <button className="ghost"> </button>
    </div>
  );
}

export default VitalCard;
