import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Line, LineChart, ResponsiveContainer } from 'recharts';
import TrendChart from '../components/Summary/TrendChart';
import VitalCard from '../components/Summary/VitalCard';
import { fetchDailySummary, fetchTrends } from '../services/apiService';

function SummaryPage() {
  const [daily, setDaily] = useState(null);
  const [trends, setTrends] = useState({ bp_trend: [], glucose_trend: [], trend_percentages: {} });
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const deltaClass = (val) => (val > 0 ? 'delta-up' : val < 0 ? 'delta-down' : 'delta-flat');
  const deltaText = (val) => `${val > 0 ? '+' : ''}${Number(val || 0).toFixed(1)}`;

  useEffect(() => {
    Promise.all([fetchDailySummary('demo-user'), fetchTrends('demo-user', 7)])
      .then(([dailyRes, trendRes]) => {
        setDaily(dailyRes.data);
        setTrends(trendRes.data || {});
      })
      .catch((err) => setError(err.message));
  }, []);

  if (error) return <div className="page"><p>{error}</p></div>;
  if (!daily) return <div className="page"><p>   ...</p></div>;

  return (
    <div className="page">
      <h1> </h1>
      <p className="subtitle">       .</p>
      <div className="status-badge level">{daily.status.status_label}</div>
      <p>{daily.summary_text}</p>

      <div className="grid two">
        <div className="card">
          <h3> vs  7 </h3>
          <div className="row between">
            <span>(SBP)</span>
            <span className={`delta-badge ${deltaClass(daily.delta_badges?.bp_systolic || 0)}`}>
              {deltaText(daily.delta_badges?.bp_systolic || 0)}
            </span>
          </div>
          <small>7  {daily.delta_badges?.bp_avg_7d ?? '-'} </small>
          <div className="row between" style={{ marginTop: 8 }}>
            <span></span>
            <span className={`delta-badge ${deltaClass(daily.delta_badges?.blood_sugar || 0)}`}>
              {deltaText(daily.delta_badges?.blood_sugar || 0)}
            </span>
          </div>
          <small>7  {daily.delta_badges?.glucose_avg_7d ?? '-'} </small>
        </div>

        <div className="card">
          <h3>  </h3>
          <div style={{ width: '100%', height: 80 }}>
            <ResponsiveContainer>
              <LineChart data={daily.confidence_trend || []}>
                <Line type="monotone" dataKey="value" stroke="#5C7CFA" dot={{ r: 2 }} strokeWidth={2} />
              </LineChart>
            </ResponsiveContainer>
          </div>
          <small>
               {Math.round(((daily.confidence_trend || []).reduce((a, c) => a + (c.value || 0), 0) /
              Math.max(1, (daily.confidence_trend || []).length)) * 100)}%
          </small>
        </div>
      </div>

      <div className="grid two">
        <VitalCard
          type="blood-pressure"
          systolic={daily.current_values.bp_systolic}
          diastolic={daily.current_values.bp_diastolic}
          status={daily.status.status_label}
        />
        <VitalCard
          type="blood-sugar"
          value={daily.current_values.blood_sugar}
          status={daily.status.status_label}
        />
      </div>

      <div className="grid two">
        <div className="card">
          <h3>  </h3>
          {(daily.quality_checklist || []).map((item) => (
            <div key={item.id} className="check-item">
              <div className="row between">
                <strong>{item.label}</strong>
                <span className={`quality ${item.status === '' ? 'good' : item.status === '' ? 'mid' : 'bad'}`}>{item.status}</span>
              </div>
              <small>{item.tip}</small>
            </div>
          ))}
        </div>

        <div className="card">
          <h3>   </h3>
          <p>{daily.daily_comment}</p>
          <button className="ghost" onClick={() => navigate('/measurement')}> </button>
        </div>
      </div>

      <div className="grid two">
        <TrendChart
          title="  "
          color="#5C7CFA"
          data={trends.bp_trend || []}
          min={100}
          max={180}
          trend={trends.trend_percentages?.bp || 0}
        />
        <TrendChart
          title="  "
          color="#9775FA"
          data={trends.glucose_trend || []}
          min={0}
          max={200}
          trend={trends.trend_percentages?.glucose || 0}
        />
      </div>

      <button onClick={() => navigate('/lifestyle')}>     </button>
    </div>
  );
}

export default SummaryPage;
