function ComparisonChart({ title, percentile, color }) {
  return (
    <div className="card comparison-card">
      <h3>{title} </h3>
      <div className="bar-wrap">
        <div className="bar low"></div>
        <div className="bar normal"> </div>
        <div className="bar high"></div>
        <div className="marker" style={{ left: `${percentile}%`, borderColor: color }} />
      </div>
      <p className="percentile"> : <strong>{percentile}%</strong></p>
    </div>
  );
}

export default ComparisonChart;
