import { Navigate, NavLink, Route, Routes } from 'react-router-dom';
import MeasurementPage from './pages/MeasurementPage';
import SummaryPage from './pages/SummaryPage';
import LifestyleGuidePage from './pages/LifestyleGuidePage';

function App() {
  return (
    <div className="app-shell">
      <header className="topbar">
        <div className="brand">
          <span className="dot" />
          <div>
            <strong>VisiVital</strong>
            <p>rPPG    </p>
          </div>
        </div>
        <nav className="tabs">
          <NavLink to="/measurement"></NavLink>
          <NavLink to="/summary"></NavLink>
          <NavLink to="/lifestyle"></NavLink>
        </nav>
      </header>

      <Routes>
        <Route path="/" element={<Navigate to="/measurement" replace />} />
        <Route path="/measurement" element={<MeasurementPage />} />
        <Route path="/summary" element={<SummaryPage />} />
        <Route path="/lifestyle" element={<LifestyleGuidePage />} />
      </Routes>
    </div>
  );
}

export default App;
